body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  background-color: grey;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.main-header{display: flex;justify-content: center;}

h1{text-align: center;color:#fff;font-size: 45px;text-shadow: 2px 2px #000;}

.gif-list{display: flex;overflow: auto;}
i{font-style: normal;}
.search-button{width: 120px;
  background: #ffc107;
  height: 38px;border-radius: 9px;
  border: none;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: normal;position: relative;
  left: 10px;
}
input{    height: 38px;border-radius: 9px;}

.gif-wrap{    border: 2px solid #ffeb3b;}

ul li{list-style-type: none;}

@media only screen and (max-width: 600px) {
  .gif-list{display: block;}
}